import React from "react";
import { Button, Card, CardHeader, CardBody, CardFooter, CardTitle, Table, FormGroup, Input, Row, Col, Form } from "reactstrap";
import API from "../API";
import cities from "../API/Cities";
import shortid from "shortid";

class AddPlace extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      places: [
        {
          _id: "",
          name: "",
          address: "",
          city: "",
          state: "",
          createdAt: "",
          updatedAt: ""
        }
      ],
      isEditing: false,
      isData: false,
      name: "",
      id: "",
      address: "",
      city: "",
      cityState: "",
      state: ""
    };
  }

  async componentDidMount() {
    const r = await API.place.get();
    this.setState({ places: r.data, isData: true });
  }

  onClick = async () => {
    const { name, address, city, isEditing, id, state } = this.state;
    try {
      if (isEditing) {
        await API.place.patch(id, name, address, city, state);
      } else {
        await API.place.post(name, address, city, state);
      }
      window.location.reload();
    } catch (error) {
      alert("Error, por favor recargue la página. Recuerde, nombre debe ser único");
    }
  };

  render() {
    return (
      <>
        <div className="content">
          <Row>
            <Col md="12">
              <Card>
                <CardHeader>
                  <CardTitle tag="h4">
                    Lugares de votación
                    {this.state.isEditing && (
                      <Button
                        className="btn-round"
                        style={{ float: "right" }}
                        size="sm"
                        color="success"
                        onClick={() => {
                          this.setState({ isEditing: false, id: "", name: "", address: "", city: "" });
                        }}
                      >
                        Nuevo lugar
                      </Button>
                    )}
                  </CardTitle>
                </CardHeader>
                <CardBody>
                  <Table className="tablesorter" responsive>
                    <thead className="text-primary">
                      <tr>
                        <th>Nombre</th>
                        <th>Dirección</th>
                        <th>Ciudad</th>
                        <th>Creado</th>
                        <th>Actualizado</th>
                        <th>Acción</th>
                      </tr>
                    </thead>
                    <tbody>
                      {this.state.isData &&
                        this.state.places.map(c => {
                          return (
                            <tr key={shortid.generate()}>
                              <td>{c.name}</td>
                              <td>{c.address}</td>
                              <td>{c.city}</td>
                              <td>{new Date(c.createdAt).toLocaleString()}</td>
                              <td>{new Date(c.updatedAt).toLocaleString()}</td>
                              <td style={{ justifyContent: "space-around", display: "flex" }}>
                                <Button
                                  className="btn-round"
                                  size="sm"
                                  color="info"
                                  onClick={() => {
                                    this.setState({
                                      isEditing: true,
                                      id: c._id,
                                      name: c.name,
                                      address: c.address,
                                      city: c.city,
                                      cityState: `${c.city}##${c.state}`
                                    });
                                  }}
                                >
                                  Editar
                                </Button>
                                <Button
                                  className="btn-round"
                                  size="sm"
                                  color="danger"
                                  onClick={async () => {
                                    try {
                                      // eslint-disable-next-line no-restricted-globals
                                      const isDelete = confirm(`¿Seguro que quierer borrar el lugar de votación ${c.name}?`);
                                      if (isDelete) {
                                        await API.place.delete(c._id);
                                        window.location.reload();
                                      }
                                    } catch (error) {
                                      alert(error);
                                    }
                                  }}
                                >
                                  Eliminar
                                </Button>
                              </td>
                            </tr>
                          );
                        })}
                    </tbody>
                  </Table>
                </CardBody>
              </Card>
              <Card>
                <CardHeader>
                  <h5 className="title">{this.state.isEditing ? "Editar lugar" : "Añadir lugar"}</h5>
                </CardHeader>
                <CardBody>
                  <Form
                    id="form"
                    onSubmit={e => {
                      e.preventDefault();
                      this.onClick();
                    }}
                  >
                    <Row>
                      <Col md="6">
                        <FormGroup>
                          <label htmlFor="name">Nombre</label>
                          <Input
                            required
                            placeholder="Nombre"
                            id="name"
                            type="text"
                            value={this.state.name}
                            onChange={e => this.setState({ name: e.target.value })}
                          />
                        </FormGroup>
                      </Col>
                      <Col md="6">
                        <FormGroup>
                          <label htmlFor="address">Dirección</label>
                          <Input
                            required
                            placeholder="Dirección"
                            id="address"
                            type="text"
                            value={this.state.address}
                            onChange={e => this.setState({ address: e.target.value })}
                          />
                        </FormGroup>
                      </Col>
                    </Row>
                    <Row>
                      <Col md="6">
                        <FormGroup>
                          <label htmlFor="city">Ciudad</label>
                          <Input
                            type="select"
                            required
                            id="city"
                            name="select"
                            value={this.state.cityState}
                            onChange={e => {
                              const [city, state] = e.target.value.split("##");
                              this.setState({ city, state, cityState: e.target.value });
                            }}
                          >
                            <option></option>
                            {cities.map(c => {
                              return c.cities.map(s => {
                                return (
                                  <option key={shortid.generate()} value={`${s}##${c.state}`} style={{ color: "black" }}>
                                    {`${c.state} - ${s}`}
                                  </option>
                                );
                              });
                            })}
                          </Input>
                        </FormGroup>
                      </Col>
                    </Row>
                  </Form>
                </CardBody>
                <CardFooter>
                  <Button className="btn-fill" color="primary" form="form" formTarget="form" type="submit">
                    {this.state.isEditing ? "Guardar" : "Añadir"}
                  </Button>
                </CardFooter>
              </Card>
            </Col>
          </Row>
        </div>
      </>
    );
  }
}

export default AddPlace;
